@import url('https://fonts.googleapis.com/css2?family=Assistant:wght@200;300;400;500;600;700;800&display=swap');
@import "../node_modules/bootstrap/dist/css/bootstrap-grid.min.css";


//Variables
$base-color: #e9c3ed;
$secondary-color: #d8c7eb;
$navlinks: #08a4a7;
$dark: #421873;



// Base styles
body {
  font-family: 'Assistant', sans-serif;
  color: $dark;
}

.App {
  display: flex;
  align-content: space-between;
  flex-direction: column;
  background: linear-gradient(180deg, #FFFFFF 0%, #F0CFF4 100%);
  height: 100%;
  min-height: 100vh;
  padding-bottom: 3rem;
}

// Navigation
header {
  padding: .5rem 1rem;
}

.Navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  a {
    text-decoration: none;
    margin: .5rem;
    color: $navlinks;
    font-weight: 600;
    padding: -.5rem;
  }
  a:visited {
    color: $navlinks;
  }
  .navlinks a:hover {
    color: lighten($navlinks, 5%);
    border-bottom: solid 2px $secondary-color;
    transition: all .125s;
  }
  ul {
    list-style: none;
    li {
      display: inline;
    }
  }
  .logo:hover {
    transform: scale(1.125);
    transition: all .25s;
  }
}

//HomeView
.HomeView {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
  .splash {
    display: flex;
    justify-content: center;
  }
  h1 {
    font-size: 100px;
    margin: 0;
  }
  button {
    padding: 1rem;
    border-radius: 100px;
    border: solid 2px $secondary-color;
    margin-right: 1rem;
    // text-transform: uppercase;
    font-weight: 700;
    letter-spacing: .45px;
    background-color: rgba($color: $secondary-color, $alpha: 0);
    img {
      height: 35px;
    }
  }
  button:hover {
    background-color: rgba($color: $secondary-color, $alpha: .25);
    transform: scale(1.125);
    transition: all .025s;
  }
  .selfie {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
    img {
      width: 100%;
      border-radius: 200px;
      margin-left: 3rem;
    }
  }
  .more-info {
    background-color: $secondary-color;
    width: 60%;
    margin: 0 auto;
    padding: 3rem;
    border-radius: 2rem;
    // height: 600px;
    margin-top: 3rem;
    // border-radius: 20%;
  }
}

.noun {
  border-bottom: solid 2px $secondary-color;
  transition: all .25s;
}

.buttons {
  margin-top: 3rem;
}

//PortfolioView
.PortfolioView {
  h1 {
    padding: .25rem;
    margin: 0;
    color: $dark;
  }
  ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    a {
      text-decoration: none;
    }
  }
  // li:last-child {
  //   width: 100%;
  // }
  li {
    padding: 1rem;
    margin-bottom: 1rem;
    text-align: center;
    background-color: lighten($secondary-color, 5%);
    // border: solid 2px darken($secondary-color, 10%);
    width: 49%;
    border-radius: 2rem;
    h4 {
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: .5px;
      font-size: 14px;
      margin: 0;
      a {
        color: $dark;
        margin: 0 .25rem;
        padding: 0 .25rem;
      }
    }
    img {
      width: 100%;
      border-radius: 2rem 2rem 0 0;
    }
  }
  li:hover {
    background-color: lighten($secondary-color, 2%);
  }
  img {
    max-width: 100%;
  }
  
}
.project-container {
  h1 {
    text-align: center;
  }
}
.Project {
  padding-bottom: 1rem;
  background-color: $secondary-color;
  width: 60%;
  max-width: 900px;
  margin: 0 auto;
  color: $dark !important;
  border-radius: 2rem;
  img {
    width: 100%;
    border-radius: 2rem 2rem 0 0;
  }
  h1 {
    text-align: center;
  }
  div {
    padding: 0 2rem;
  }
  .details {
    text-transform: uppercase;
    background-color: lighten($secondary-color, 5%);
    padding: 1rem 3rem;
    padding-left: 4rem;
    margin: 1rem -4rem;
    // border-radius: 2rem;
  }
  
}
.feature::after {
  content: " | ";
}
.feature:last-child::after {
  content: "";
}
.project-buttons {
  display: flex;
  justify-content: center;
  margin: 1rem;
  a {
    padding: 1rem;
    border: solid 2px darken($secondary-color, 10%);
    margin-right: 1rem;
    font-weight: 700;
    letter-spacing: .45px;
    background-color: lighten($secondary-color, 2%);
    text-decoration: none;
    color: $dark;
    border-radius: 2rem;
  }
  a:hover{
    background-color: lighten($secondary-color, 5%);
  }
}
//AboutView
.AboutView {
  width: 60%;
  background-color: $secondary-color;
  margin: 0 auto;
  padding: 1rem;

}