@import url(https://fonts.googleapis.com/css2?family=Assistant:wght@200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



body{font-family:'Assistant', sans-serif;color:#421873}.App{display:flex;align-content:space-between;flex-direction:column;background:linear-gradient(180deg, #fff 0%, #F0CFF4 100%);height:100%;min-height:100vh;padding-bottom:3rem}header{padding:.5rem 1rem}.Navbar{display:flex;justify-content:space-between;align-items:center}.Navbar a{text-decoration:none;margin:.5rem;color:#08a4a7;font-weight:600;padding:-.5rem}.Navbar a:visited{color:#08a4a7}.Navbar .navlinks a:hover{color:#09bcbf;border-bottom:solid 2px #d8c7eb;transition:all .125s}.Navbar ul{list-style:none}.Navbar ul li{display:inline}.Navbar .logo:hover{transform:scale(1.125);transition:all .25s}.HomeView{width:80%;max-width:1200px;margin:0 auto}.HomeView .splash{display:flex;justify-content:center}.HomeView h1{font-size:100px;margin:0}.HomeView button{padding:1rem;border-radius:100px;border:solid 2px #d8c7eb;margin-right:1rem;font-weight:700;letter-spacing:.45px;background-color:rgba(216,199,235,0)}.HomeView button img{height:35px}.HomeView button:hover{background-color:rgba(216,199,235,0.25);transform:scale(1.125);transition:all .025s}.HomeView .selfie{display:flex;justify-content:center;align-items:center;width:30%}.HomeView .selfie img{width:100%;border-radius:200px;margin-left:3rem}.HomeView .more-info{background-color:#d8c7eb;width:60%;margin:0 auto;padding:3rem;border-radius:2rem;margin-top:3rem}.noun{border-bottom:solid 2px #d8c7eb;transition:all .25s}.buttons{margin-top:3rem}.PortfolioView h1{padding:.25rem;margin:0;color:#421873}.PortfolioView ul{list-style:none;display:flex;flex-wrap:wrap;justify-content:space-between}.PortfolioView ul a{text-decoration:none}.PortfolioView li{padding:1rem;margin-bottom:1rem;text-align:center;background-color:#e5daf2;width:49%;border-radius:2rem}.PortfolioView li h4{font-weight:600;text-transform:uppercase;letter-spacing:.5px;font-size:14px;margin:0}.PortfolioView li h4 a{color:#421873;margin:0 .25rem;padding:0 .25rem}.PortfolioView li img{width:100%;border-radius:2rem 2rem 0 0}.PortfolioView li:hover{background-color:#ddcfee}.PortfolioView img{max-width:100%}.project-container h1{text-align:center}.Project{padding-bottom:1rem;background-color:#d8c7eb;width:60%;max-width:900px;margin:0 auto;color:#421873 !important;border-radius:2rem}.Project img{width:100%;border-radius:2rem 2rem 0 0}.Project h1{text-align:center}.Project div{padding:0 2rem}.Project .details{text-transform:uppercase;background-color:#e5daf2;padding:1rem 3rem;padding-left:4rem;margin:1rem -4rem}.feature::after{content:" | "}.feature:last-child::after{content:""}.project-buttons{display:flex;justify-content:center;margin:1rem}.project-buttons a{padding:1rem;border:solid 2px #bea1de;margin-right:1rem;font-weight:700;letter-spacing:.45px;background-color:#ddcfee;text-decoration:none;color:#421873;border-radius:2rem}.project-buttons a:hover{background-color:#e5daf2}.AboutView{width:60%;background-color:#d8c7eb;margin:0 auto;padding:1rem}

